#header {
  float: left;
  width: 100%;
  position: absolute;
  background-color: white;
  left: 0;
  top: 10px;
  z-index: 999;
  padding: 30px 20px;
  transition-duration: 0.4s;
  -webkit-transition-duration: 0.4s;
  // border-top: 12px solid #ca2825;
  -moz-transition-duration: 0.4s;
  transition-property: all;
  -webkit-transition-property: all;
  -moz-transition-property: all;
  transition-timing-function: cubic-bezier(0.7, 1, 0.7, 1);
  -webkit-transition-timing-function: cubic-bezier(0.7, 1, 0.7, 1);
  -moz-transition-timing-function: cubic-bezier(0.7, 1, 0.7, 1);
  @include transition(all 0.3s);
  &.sticky {
    position: -webkit-sticky !important;
    position: fixed;
    background: #fff;
    padding: 10px 10px;
    @include box-shadow(0 0 10px rgba(0, 0, 0, 0.1));
    top: 0;
    .header .header-main {
      .logo {
        width: 280px;
      }
    }
  }
}
.header {
  @extend %section;
  .header-wrap {
    padding: 0 20px;
  }
  .header-main {
    position: relative;
    @extend %section;
    @include transition(all 0.3s);
    .logo {
      float: left;
      width: 360px;
      @include transition(all 0.3s);
      a {
        width: 100%;
        float: left;
        img {
          width: 100%;
        }
      }
    }
  }
  .header-right {
    float: right;
    width: calc(100% - 360px);
    text-align: center;
    padding: 35px 0 26px;
    @include transition(all 0.3s);
    .nav-main {
      margin-left: -50px;
      float: none;
      width: auto;
      display: inline-block;
      @include transition(all 0.3s);
      .menu-main-menu-container {
        @extend %section;
      }
      ul.menu {
        height: auto;
        @include transition(all 0.3s);
        .menu-item-39 ul.sub-menu {
          width: 150px;
        }
        > li {
          float: left;
          width: auto;
          position: relative;
          margin-right: 50px;
          &:hover {
            ul.sub-menu {
              visibility: visible;
              opacity: 1;
              top: 100%;
              display: block !important;
            }
          }

          &.current-menu-item {
            a {
              // color:  #ca2825;
              &::before {
                -webkit-transform: scaleX(1);
                transform: scaleX(1);
              }
            }
            ul.sub-menu {
              li {
                a {
                  &::before {
                    -webkit-transform: scaleX(0);
                    transform: scaleX(0);
                  }
                }
              }
            }
          }
          &:last-child {
            margin-right: 0px;
          }
          a {
            float: left;
            width: auto;
            height: auto;
            font-style: normal;
            font-size: 24px;
            font-family: "Poppins", sans-serif;
            line-height: 120%;
            font-weight: 500;
            display: block;
            align-items: center;
            color: #2b3a67;
            text-decoration: none;
            @include transition(all 0.25s ease 0s);
            position: relative;
            padding: 5px 0px;
            overflow: hidden;
            &::before {
              left: 0;
              bottom: 0;
              width: 100%;
              height: 2px;
              background: #ca2825;
              -webkit-transform: scaleX(0);
              transform: scaleX(0);
              -webkit-transition: all 0.25s ease 0s;
              transition: all 0.25s ease 0s;
              content: "";
              position: absolute;
            }
            &:hover,
            &:active,
            &:focus {
              // color:  #ca2825;
              &::before {
                -webkit-transform: scaleX(1);
                transform: scaleX(1);
              }
            }
          }
          ul.sub-menu {
            background-color: #fff;
            width: 185px;
            position: absolute;
            opacity: 0;
            visibility: hidden;
            left: 0;
            top: 150%;
            // box-shadow: 2px 2px 3px rgba(0, 0, 0,0.20);
            // -webkit-box-shadow: 2px 2px 3px rgba(0, 0, 0,0.20);
            // -moz-box-shadow: 2px 2px 3px rgba(0, 0, 0, 0.2);
            transition: all 0.35s ease 0s;
            -webkit-transition: all 0.35s ease 0s;
            -moz-transition: all 0.35s ease 0s;
            li {
              float: left;
              width: 100%;
              position: relative;
              a {
                float: left;
                width: 100%;
                height: 100%;
                font-style: normal;
                font-size: 20px;
                font-family: "Poppins", sans-serif;
                line-height: 120%;
                font-weight: 500;
                display: block;
                align-items: left;
                color: #2b3a67;
                text-decoration: none;
                @include transition(all 0.25s ease 0s);
                position: relative;
                padding: 5px 0px;
                overflow: hidden;
                text-align: left;
                // &:hover{
                // 	color:  #ca2825;
                // }
                &:hover,
                &:active,
                &:focus {
                  // color:  #ca2825;
                  &::before {
                    -webkit-transform: scaleX(1);
                    transform: scaleX(1);
                  }
                }
                &:active {
                  &::before {
                    -webkit-transform: scaleX(1);
                    transform: scaleX(1);
                  }
                }
              }
              &.current-menu-item {
                a {
                  // color:  #ca2825;
                  &::before {
                    -webkit-transform: scaleX(1);
                    transform: scaleX(1);
                  }
                }
              }
            }
          }
        }
      }
    }
    .user-menu {
      width: auto;
      float: right;
      position: relative;
      .dropdown {
        position: absolute;
        // top: 50%;
        // left: 50%;
        // transform: translate(-50%, -50%);
      }
      button {
        // position: relative;
        width: 35px;
        font-size: 24px;
        background: transparent;
        border: none;
        box-shadow: none;
        outline: none;
        cursor: pointer;
        color: #fff;
      }
      ul {
        position: relative;
        margin: 0;
        padding: 0;
        width: 100%;
        background: #e7e7e7;
        transform-origin: top;
        transform: perspective(1000px) rotateX(-90deg);
        transition: 0.5s;
        margin-top: 10px;
        @include border-radius(9px);
      }
      ul.active {
        transform: perspective(1000px) rotateX(0deg);
      }
      ul li {
        list-style: none;
        padding: 0 10px;
        transition: all 0.5s;
      }
      ul li a {
        display: block;
        padding: 10px;
        text-align: right;
        text-decoration: none;
        color: #333333;
        border-bottom: 1px solid rgba(255, 255, 255, 1);
        transition: all 0.5s;
      }
      ul {
        li {
          &:last-child {
            a {
              border-bottom: none;
            }
          }
        }
      }
      ul li:hover {
        background: #b6c7f6;
        @include border-radius(9px);
      }
    }
  }
}
